<template>
	<div class="body-wrapper">
		<div class="title-wrapper">实时访问情况</div>
		<div class="select-wrapper">
			<div class="select-group">
				<span class="select-group-title">来源IP：</span>
				<div class="select-group-item">
					<el-select placeholder="请选择来源IP" v-model="value" clearable size="small">
						<el-option v-for="(item, index) in list" :value="item" :key="index"></el-option>
					</el-select>
				</div>
			</div>
			<!-- <div class="select-group">
				<span class="select-group-title">间隔(s)：</span>
				<div class="select-group-item">
					<el-input v-model.trim="gap" clearable size="small" placeholder="请输入间隔时间"></el-input>
				</div>
			</div> -->
			<div class="select-button" @click="searchButton">查询</div>
		</div>
		<div class="chart-wrapper">
			<div ref="myChart" style="height: 400px;"></div>
		</div>
	</div>
</template>

<script>

export default {
	data(){
		return {
			value: '',
			list: ['192.168.1.1', '192.168.1.2', '192.168.1.3'],
			myChart: '',
			//x轴数据
			xData: [],
			//呈现的数据
			data: [],
			//定时器
			interval: '',
			//定时器间隔
			gap: 60,
			//定时器触发标识
			intervalFlag: true
		}
	},
	created(){

	},
	mounted() {
		this.myChart = this.$echarts.init(this.$refs.myChart);

		window.onresize = () => {
			this.myChart.resize({width: 'auto'});
		}

		//获取数据
		this.searchButton();

	},
	methods: {
		getData() {
			
			setTimeout(() => {

				let cur = this.$utils.formatDate(new Date());
				let curNum = Math.ceil(Math.random() * 9) + 1;

				this.xData.push(cur);
				this.data.push(curNum);

				this.draw();
			}, 200)

			return

			// this.$axios.post({
			// 	url: '/mls-np/example/hello',
			// 	data: {
			// 		//实时IP
			// 		realTimeIp: this.value
			// 	},
			// 	success: (res) => {
			// 		console.log('----');
			// 		console.log(res);

			// 		/* 
			// 			this.xData = [];
			// 			this.reqList = [];
			// 			this.errorList = [];
			// 		*/

			// 		this.draw();

			// 	}
			// })
		},

		searchButton() {

			if (this.myChart) {
				this.myChart.showLoading();
				clearInterval(this.interval);

				//清空数据
				this.xData = [];
				this.data = [];

				//图标标题赋值(更新)
				let title = '';
				if (this.value) {
					title = this.value;
				}
				else {
					title = '实时访问情况'
				}
				this.myChart.setOption({
					title: {
						text: title
					}
				});

				this.getData();


				//默认往前取10分钟
				let historyX = [];
				let historyD = [];
				let hcur = new Date();
				let hs = setInterval(() => {

					if (this.intervalFlag) {
						this.intervalFlag = false;

						setTimeout(() => {
							this.intervalFlag = true;
						}, 200)

						let curNum = Math.ceil(Math.random() * 9) + 1;
						hcur.setMinutes(hcur.getMinutes() - 1);
						let hstr = this.$utils.formatDate(hcur);
	
						historyX.push(hstr);
						historyD.push(curNum);

					}

					if (historyX.length == 10) {
						historyX.reverse();
						historyD.reverse();
						this.xData.unshift(...historyX);
						this.data.unshift(...historyD);

						this.draw();

						console.log(this.xData);
						console.log(this.data);
						clearInterval(hs);
					}
				}, 200)


				//正常1分钟一次取当前数据
				this.interval = setInterval(() => {

					this.getData();
					
				}, 1000 * Number(this.gap))
			}
		},
		//画图
		draw() {
			this.myChart.hideLoading();

			let option = {
				color: ['pink'],
				tooltip: {
					trigger: 'axis'
				},
				legend: {
					data: ['数据']
				},
				grid: {
					left: '5%',
					right: '5%'
				},
				xAxis: {
					type: 'category',
					axisTick: {
						alignWithLabel: true
					},
					data: this.xData
				},
				yAxis: {
					type: 'value',
					name: '次数',
					nameGap: '10',
					axisLabel: {
						formatter: '{value} 次'
					}
				},
				series: [{
					name: '数据',
					type: 'line',
					data: this.data,
					markPoint: {
							data: [
								{type: 'max', name: '最大值'},
								// {type: 'min', name: '最小值'}
							]
            },
				}]
			};

			this.myChart.setOption(option);
		},
	}
}
</script>

<style lang="less" scoped>

</style>



