import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';
import { Loading, Message } from 'element-ui';

let loadingInstance;
var CancelToken = axios.CancelToken;
var source = CancelToken.source();


//loading
function startLoading() {
	loadingInstance = Loading.service({
		fullscreen: true,
		background: "rgba(0, 0, 0, .2)"
	})
}

//默认公共项
axios.defaults.baseURL = 'http://localhost:8999/';


//-------------------请求拦截
//请求发起前
axios.interceptors.request.use(
	config => { //请求配置项

		return config;

	},

	// 请求前请求后发生的error均会被请求catch捕获到
	err => Promise.reject(err)
);

//请求完成后
axios.interceptors.response.use(
	response => { //response值请求回内容

		//去除loading效果
		if (loadingInstance) {
			loadingInstance.close();
		}

		return response;
	},

	//可结合请求code码进行操作
	err => {

		//提示
		// Message({
		// 	message: err,
		// 	type: 'error',
		// 	duration: 1500
		// })

		// Promise.reject(err);
		return document.write(`页面出现了异常,请将下面内容截图联系工作人员\n ${err}`);
	}
)
//http://www.jianshu.com/p/df464b26ae58
//将axios方法添加到vue实例的原型链中(this.$axios触发)


//封装请求方法
const http = {
	get(option = {}){
		/*
			param:
			url       请求地址
			success   成功回调
			loading   是否需要loading的标识(传true使用默认)
		*/

		//添加loading标识
		if(option.loading){
			startLoading();
		}

		let url = option.url;
		if(option.data){
			url += `?${qs.stringify(option.data)}`
		}

		return (
			axios.get(url)
			.then(option.success)
			.catch((err) => {

				// document.write(`页面出现了异常,请将下面内容截图联系工作人员\n ${err}`);
			})
		)
	},

	post(option = {}){
		/*
			param:
			url       请求地址
			success   成功回调
			loading   是否需要loading的标识(传true使用默认)
		*/

		//添加loading标识
		if(option.loading){
			startLoading();
		}

		let config = Object.assign({}, option.config, {
			cancelToken: source.token
		})

		return (
			axios.post(option.url, qs.stringify(option.data), config)
			.then(option.success)
			.catch((err) => {

				// document.write(`页面出现了异常,请将下面内容截图联系工作人员\n ${err}`);
			})
		)
	},

	formData_post(option = {}){
		/*
			param:
			url       请求地址
			success   成功回调
			loading   是否需要loading的标识(传true使用默认)
		*/

		//添加loading标识
		if(option.loading){
			startLoading();
		}

		return (
			axios.post(option.url, option.data, {
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			})
			.then(option.success)
			.catch((err) => {

				// document.write(`页面出现了异常,请将下面内容截图联系工作人员\n ${err}`);
			})
		)
	},

	all(req = [], success){
		/*
			req       触发axios方法的各个axios请求
			success   成功回调
		*/
		return(
			axios.all(req)
			.then(axios.spread(success))
			.catch((err) => {
				// document.write(`页面出现了异常,请将下面内容截图联系工作人员\n ${err}`);
				
			})
		)
	},

	stop() {
		source.cancel();
	}
}



Vue.prototype.$axios = http;


