<template>
	<div class="body-wrapper">
		<div class="title-wrapper">URL响应统计</div>
		<div class="select-wrapper">
			<div class="select-group">
				<span class="select-group-title">时间范围：</span>
				<div class="select-group-item">
					<el-date-picker
						v-model="dateValue"
						type="datetimerange"
						format="yyyy/MM/dd HH:mm"
						range-separator="至"
						start-placeholder="开始日期"
						end-placeholder="结束日期"
						size="small"
						@blur="dateChange">
					</el-date-picker>
				</div>
			</div>
			<div class="select-group">
				<span class="select-group-title">URL：</span>
				<div class="select-group-item">
					<el-select placeholder="请选择URL" v-model="value" clearable size="small">
						<el-option v-for="(item, index) in list" :value="item" :key="index"></el-option>
					</el-select>
				</div>
			</div>
			<div class="select-button" @click="getData">查询</div>
		</div>
		<div class="chart-wrapper">
			<div ref="myChart" style="height: 400px;"></div>
		</div>
	</div>
</template>

<script>

export default {
	data(){
		return {
			value: '',
			list: ['/a', '/b', '/c'],
			dateValue: [],
			myChart: '',
			//x轴数据
			xData: [],
			//最大响应时长
			max: [],
			//最小响应时长
			min: [],
			//平均
			average: [],
			//90%响应时长
			percent: []
		}
	},
	computed: {
		// ...mapGetters(['count'])
	},
	created(){

	},
	
	mounted() {
		this.myChart = this.$echarts.init(this.$refs.myChart);

		window.onresize = () => {
			this.myChart.resize({width: 'auto'});
		}

		// this.xData = ['2018/12/10', '2018/12/11', '2018/12/12', '2018/12/13'];
		// this.max = [10, 11, 15, 20];
		// this.min = [2, 3, 1, 2];
		// this.average = [6, 7, 8, 11];
		// this.percent = [4, 5, 7, 8];

		this.draw();

	},
	methods: {
		//时间选择
		dateChange() {
			if (this.dateValue && this.dateValue.length == 2) {
				console.log('---选择---');
				console.log('开始时间', this.$utils.formatDate(this.dateValue[0]));
				console.log('结束时间', this.$utils.formatDate(this.dateValue[1]));
			}
		},
		getData() {

			if (!this.dateValue || this.dateValue.length != 2) {
				this.$message({
					message: '请选择时间范围',
					type: 'error',
					duration: 1500
				})
				return
			}

			if (!this.value) {
				this.$message({
					message: '请选择URL',
					type: 'error',
					duration: 1500
				})
				return
			}


			if (this.myChart) {
				this.myChart.showLoading();
				this.xData = [];
				this.max = [];
				this.min = [];
				this.average = [];
				this.percent = [];
				this.myChart.clear();


				this.$axios.post({
					url: '/mls-np/example/hello',
					data: {
						//开始时间
						beginTime: this.$utils.formatDate(this.dateValue[0]),
						//结束时间
						endTime: this.$utils.formatDate(this.dateValue[1]),
						//URL
						url: this.value
					},
					success: (res) => {
						console.log('----');
						console.log(res);

						/* 
							this.xData = [];
							this.max = [];
							this.min = [];
							this.average = [];
							this.percent = [];
						*/

						this.draw();

					}
				})

				// setTimeout(() => {
				// 	this.myChart.hideLoading();
				// 	this.draw();

				// }, 1000)
				
			}
		},
		draw() {

			this.myChart.hideLoading();

			let option = {
				tooltip: {
					trigger: 'axis'
				},
				legend: {
					data: ['最大响应时长', '最小响应时长', '平均响应时长', '90%响应时长']
				},
				xAxis: {
					type: 'category',
					data: this.xData
				},
				yAxis: {
					type: 'value',
					name: '时间',
					nameGap: '10',
					axisLabel: {
						formatter: '{value} s'
					}
				},
				series: [
					{
						name: '最大响应时长',
						type: 'bar',
						barGap: 0,
						data: this.max
					},
					{
							name: '最小响应时长',
							type: 'bar',
							barGap: 0,
							data: this.min
					},
					{
							name: '平均响应时长',
							type: 'line',
							data: this.average,
					},
					{
							name: '90%响应时长',
							type: 'line',
							data: this.percent,
					}
				]
			}

			if (this.value) {
				option['title'] = {
					text: `URL: ${this.value}`
				}
			}

			this.myChart.setOption(option)

		}
	}
}
</script>

<style lang="less" scoped>

</style>



