<template>
  <div id="app">
    <div class="menu-wrapper">
      <el-menu class="menu-group" router :default-active="curPath">
        <el-menu-item index="/">
          <i class="el-icon-time"></i>
          <span slot="title">实时监控</span>
        </el-menu-item>
        <el-menu-item index="/b">
          <i class="el-icon-menu"></i>
          <span slot="title">目标IP</span>
        </el-menu-item>
        <el-menu-item index="/c">
          <i class="el-icon-menu"></i>
          <span slot="title">源IP</span>
        </el-menu-item>
        <el-menu-item index="/d">
          <i class="el-icon-tickets"></i>
          <span slot="title">URL请求错误</span>
        </el-menu-item>
        <el-menu-item index="/e">
          <i class="el-icon-tickets"></i>
          <span slot="title">URL请求响应</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="content-wrapper">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>


export default {
  data() {
    return {
      curPath: '/'
    }
  },
  created() {
    let cp = this.$route.path;
    if (this.curPath != cp) {
      this.curPath = cp;
    }
  }
}
</script>

<style lang="less">


#app {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;

  .menu-wrapper {
    width: 180px;
    height: 100%;
    flex: 0 0 180px;

    .menu-group {
      height: 100%;
    }
  }

  .content-wrapper {
    background: #f3f3f3;
    padding: 20px;
    flex: 1;
  }
}

</style>



