<template>
	<div class="body-wrapper">
		<div class="title-wrapper">目标IP统计</div>
		<div class="select-wrapper">
			<div class="select-group">
				<span class="select-group-title">时间范围：</span>
				<div class="select-group-item">
					<el-date-picker
						v-model="dateValue"
						type="datetimerange"
						format="yyyy/MM/dd HH:mm"
						range-separator="至"
						start-placeholder="开始日期"
						end-placeholder="结束日期"
						size="small"
						@blur="dateChange">
					</el-date-picker>
				</div>
			</div>
			<div class="select-group">
				<span class="select-group-title">目标IP：</span>
				<div class="select-group-item">
					<el-select placeholder="请选择目标IP" v-model="value" clearable size="small">
						<el-option v-for="(item, index) in list" :value="item" :key="index"></el-option>
					</el-select>
				</div>
			</div>
			<div class="select-button" @click="getData">查询</div>
		</div>
		<div class="chart-wrapper">
			<div ref="myChart" style="height: 400px;"></div>
		</div>
	</div>
</template>

<script>

export default {
	data(){
		return {
			value: '',
			list: ['192.168.1.1', '192.168.1.2', '192.168.1.3'],
			dateValue: [],
			myChart: '',
			//x轴数据
			xData: [],
			//请求数
			reqList: [],
			//错误数
			errorList: []
		}
	},
	created(){

	},
	mounted() {
		this.myChart = this.$echarts.init(this.$refs.myChart);

		window.onresize = () => {
			this.myChart.resize({width: 'auto'});
		}

		this.draw();

	},
	methods: {
		//时间选择
		dateChange() {
			if (this.dateValue && this.dateValue.length == 2) {
				console.log('---选择---');
				console.log('开始时间', this.$utils.formatDate(this.dateValue[0]));
				console.log('结束时间', this.$utils.formatDate(this.dateValue[1]));
			}
		},
		getData() {

			if (!this.dateValue || this.dateValue.length != 2) {
				this.$message({
					message: '请选择时间范围',
					type: 'error',
					duration: 1500
				})
				return
			}

			if (!this.value) {
				this.$message({
					message: '请选择目标IP',
					type: 'error',
					duration: 1500
				})
				return
			}

			

			if (this.myChart) {
				this.myChart.showLoading();
				this.xData = [];
				this.reqList = [];
				this.errorList = [];
				this.myChart.clear();


				// if (this.dateValue && this.dateValue.length == 2) {
				// 	console.log('----');
				// 	console.log('开始时间', this.$utils.formatDate(this.dateValue[0]));
				// 	console.log('结束时间', this.$utils.formatDate(this.dateValue[1]));
				// }

				this.$axios.post({
					url: '/mls-np/example/hello',
					data: {
						//开始时间
						beginTime: this.$utils.formatDate(this.dateValue[0]),
						//结束时间
						endTime: this.$utils.formatDate(this.dateValue[1]),
						//目标IP
						destIp: this.value
					},
					success: (res) => {
						console.log('----');
						console.log(res);

						/* 
							this.xData = [];
							this.reqList = [];
							this.errorList = [];
						*/

						this.draw();

					}
				})

				

				// setTimeout(() => {
				// 	this.xData = ['2018/12/10', '2018/12/11', '2018/12/12', '2018/12/13'];
				// 	this.reqList = [10, 11, 15, 20];
				// 	this.errorList = [0, 3, 1, 2];


				// }, 1000)
				
			}
		},
		draw() {
			this.myChart.hideLoading();

			let option = {
				color: ['#5fb4e3', '#f75752'],
				tooltip: {
					trigger: 'axis'
				},
				legend: {
					data: ['请求数', '错误数']
				},
				grid: {
					left: '5%',
					right: '5%'
				},
				xAxis: {
					type: 'category',
					axisTick: {
						alignWithLabel: true
					},
					data: this.xData
				},
				yAxis: {
					type: 'value',
					name: '次数',
					nameGap: '10',
					// splitLine: {
					// 	show: false
					// },
					axisLabel: {
						formatter: '{value} 次'
					}
				},
				series: [
					{
						name: '请求数',
						type: 'line',
						data: this.reqList
					},
					{
						name: '错误数',
						type: 'line',
						data: this.errorList,
						// markPoint: {
						// 	data: [
						// 		{type: 'max', name: '最大值'},
						// 		{type: 'min', name: '最小值'}
						// 	]
            // },
					}
				]
			}

			if (this.value) {
				option['title'] = {
					text: `目标IP: ${this.value}`
				}
			}

			this.myChart.setOption(option)

		},
		
	}
}
</script>

<style lang="less" scoped>

</style>



