import Vue from 'vue';

const utils = {
	//给dom加类名
	addClass(el, className) {
		if(this.hasClass(el, className)) {
			return
		}

		let newClass = el.className.split(' ');
		newClass.push(className);
		el.className = newClass.join(' ');
	},
	//检测dom是否有当前的类
	hasClass(el, className) {
		let reg = new RegExp('(^|\\s)' + className + '(\\s|$)');
		return reg.test(el.className);
	},
	//给dom去掉类
	removeClass(el, className) {
		if(!this.hasClass(el, className)) {
			return
		}

		let newClass = el.className.split(' ');
		let index = newClass.indexOf(className);
		newClass.splice(index, 1);
		
		el.className = newClass.join(' ');
	},

	// //获取当前月份
	// getCurrentMonth(split = '-'){
	// 	var date = new Date();
	// 	return this.formatDate(date, 'month');
	// },
	// //获取当前日期
	// getCurrentDay(split = '-'){
	// 	var date = new Date();
	// 	return this.formatDate(date, 'day');
	// },

	//格式化时间(YYYY-MM-DD HH:MM:SS)
	formatDate(date ,split = '-', time = ':'){

		var cur_year = date.getFullYear();
		var cur_month = date.getMonth() + 1;
		var cur_day = date.getDate();
		var curHrs = date.getHours();
		var curMins = date.getMinutes();
		// var curSecond = date.getSeconds();
		var str = '';

		cur_month = cur_month >= 10? cur_month : `0${cur_month}`;
		curHrs = curHrs < 10? `0${curHrs}`: curHrs;
		curMins = curMins < 10? `0${curMins}`: curMins;
		// curSecond = curSecond < 10? `0${curSecond}`: curSecond;

		str = `${cur_year}${split}${cur_month}${split}${cur_day} ${curHrs}${time}${curMins}`;

		return str;
	},

	//检测是否为空对象
	emptyObject(obj = {}){
		/*
			参数: 对象
			返回: true空对象 false非空对象

		 */
		for(let i in obj){
			return false;
		}

		return true;
	},

	//校验手机号码
	checkPhone(phone){
		var reg = /^[1][3,4,5,7,8][0-9]{9}$/;

		return reg.test(phone);
	},


}

Vue.prototype.$utils = utils;


